import { Component, OnInit } from '@angular/core';
import { TeacherService } from "./teacher.service";
import { SubjectService } from "../subjects/subject.service";

@Component({
  selector: 'app-add-teacher',
  templateUrl: './add-teacher.component.html',
  styleUrls: ['./add-teacher.component.css']
})
export class AddTeacherComponent implements OnInit {

  constructor(private _teacherService :TeacherService,private _subjectService:SubjectService) { }

  submitted:boolean;
  subjects:any[];
  subjectId:number;
  name:string;
  onSubmit() { this.submitted = true;
                this._teacherService.addTeacher(this.name,this.subjectId).subscribe()
         }

  ngOnInit() {
    this.submitted=false;
      this._subjectService.getSubjects().subscribe(result=>{
                                            //  this.allBatches=result.Batches;
                                               this.subjects=result
                                                  }
                                                )
  }

}



