import { Component, OnInit } from '@angular/core';
import { CourseService } from "../course.service";
import { ActivatedRoute } from "@angular/router";

@Component({
  selector: 'app-selected-batch-students',
  templateUrl: './selected-batch-students.component.html',
  styleUrls: ['./selected-batch-students.component.css']
})
export class SelectedBatchStudentsComponent implements OnInit {
cid:number;
bid:number;
batch:{};
newname:string;
students:any[];
  constructor(private _courseService:CourseService,private route: ActivatedRoute) { }
  ngOnInit() {
      this.cid = +this.route.snapshot.paramMap.get('id');
      this.bid = +this.route.snapshot.paramMap.get('bid');
      console.log(this.cid)
      console.log(this.bid)
      this._courseService.getBatchbyId(this.cid,this.bid).subscribe((result)=>{
          this.batch=result;
      })
        this._courseService.getStudents(this.cid,this.bid).subscribe((result)=>{
            this.students=result
        })
       
  }
  onSubmit(){
       this._courseService.addStudent(this.cid,this.bid,this.newname).subscribe((result)=>{
            this._courseService.getStudents(this.cid,this.bid).subscribe((result)=>{
            this.students=result
        })
        })
  }

}



