import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { Location } from '@angular/common';
import { CourseService } from "./course.service";
import { TeacherService } from "../teachers/teacher.service";
import { StudentService } from "../students/student.service";
import { SubjectService } from "../subjects/subject.service";
@Component({
  selector: 'app-selectedbatch',
  templateUrl: './selectedbatch.component.html',
  styleUrls: ['./selectedbatch.component.css']
})
export class SelectedbatchComponent implements OnInit {
cid:number;
bid:number;
newname:string='';
subjectId:number;
subjects:any[];
teachers:any[];
teacherId:number;
batch:{};
lectures:any[];
  constructor(private _courseService:CourseService,private route: ActivatedRoute,private _teacherService:TeacherService,private _subjectService:SubjectService) { }
  ngOnInit() {
      this.cid = +this.route.snapshot.paramMap.get('id');
      this.bid = +this.route.snapshot.paramMap.get('bid');
      console.log(this.cid)
      console.log(this.bid)
      this._courseService.getBatchbyId(this.cid,this.bid).subscribe((result)=>{
          this.batch=result;
      })
        this._courseService.getLectures(this.cid,this.bid).subscribe((result)=>{
            this.lectures=result
        })
      this._teacherService.getTeachers().subscribe(result=>{
                                               this.teachers=result
                                                  }
                                                )
      this._subjectService.getSubjects().subscribe(result=>{
                                               this.subjects=result
                                                  }
                                                )
  }
 onSubmit(){
       this._courseService.addLecture(this.cid,this.bid,this.newname,this.subjectId,this.teacherId).subscribe((result)=>{
              this._courseService.getLectures(this.cid,this.bid).subscribe((result)=>{
            this.lectures=result
        })
        })
  }
}



