import { Component, OnInit, Input } from '@angular/core';
import { CourseService } from "./course.service";
import { ActivatedRoute } from "@angular/router";
import { Location } from '@angular/common';

@Component({
  selector: 'app-selected-course',
  templateUrl: './selected-course.component.html',
  styleUrls: ['./selected-course.component.css']
})
export class SelectedCourseComponent implements OnInit {
cid:number;
newbatch:string='';
batchstartdate='';
course:{};
batches:any[];
  constructor(private _courseService:CourseService,private route: ActivatedRoute) { }
  onSubmit(){
        this._courseService.addBatch(this.newbatch,this.batchstartdate,this.cid).subscribe((res)=>{
              this._courseService.getBatches(this.cid).subscribe((result)=>{
            this.batches=result
        })
        })
  }
  ngOnInit() {
      this.cid = +this.route.snapshot.paramMap.get('id');
      this._courseService.getCoursebyId(this.cid).subscribe((result)=>{
          this.course=result
      })
        this._courseService.getBatches(this.cid).subscribe((result)=>{
            this.batches=result
        })
  }

}



