import { Component, OnInit } from '@angular/core';
import { CourseService } from "../courses/course.service";
import { SubjectService } from "./subject.service";

@Component({
  selector: 'app-add-subject',
  templateUrl: './add-subject.component.html',
  styleUrls: ['./add-subject.component.css']
})
export class AddSubjectComponent implements OnInit {
  
  constructor(private _subjectService:SubjectService,private _courseService:CourseService) { }
  submitted:boolean;
  courses:any[];
  courseId:number;
  name:string;
  onSubmit() { this.submitted = true;
                console.log("i am in submit")
                console.log(this.name),
                console.log(this.courseId)
                this._subjectService.addSubject(this.name,this.courseId).subscribe()
         }
  ngOnInit(){
     this.submitted=false;
      this._courseService.getCourses().subscribe(result=>{
                                            //  this.allBatches=result.Batches;
                                               this.courses=result
                                                  }
                                                )
  }
}



