import { Component, OnInit } from '@angular/core';
import { BatchService } from "./batch.service";

@Component({
  //selector: 'app-batch',
  templateUrl: './batch.component.html',
  styleUrls: ['./batch.component.css']
})
export class BatchComponent implements OnInit {
  allBatches:any[]
  upcomingBatches:any[]
  constructor(private _batchService:BatchService) { 
    this.allBatches=[],
    this.upcomingBatches=[]
  }

  ngOnInit() {
      this._batchService.getBatches().subscribe(result=>{console.log(result)
                                            //  this.allBatches=result.Batches;
                                               this.upcomingBatches=result.filter((batch)=>{
                                                    return batch.startAt>new Date().toISOString().slice(0,10)
                                               })
                                                  }
                                                )
  }

}



