import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from "@angular/common/http";
import { Observable } from "rxjs/Observable";
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/catch';

@Injectable()
export class CourseService {

   private _courseUrl:string='/courses';
  constructor(private _http:HttpClient) { }
   getCourses(){
        return this._http.get<any>(this._courseUrl)
                   .catch(this.handleError)
    }
    addCourse(cname){
        return this._http.post(this._courseUrl,{name:cname});
        
    }
    getCoursebyId(cid){
        return this._http.get<any>(this._courseUrl+'/'+cid)
                   .catch(this.handleError)
    }
    getBatches(cid){
        return this._http.get<any>(this._courseUrl+'/'+cid+'/batches')
                   .catch(this.handleError)
    }
    addBatch(bname,bdate,cid){
        return this._http.post(this._courseUrl+'/'+cid+'/batches',{name:bname,startAt:bdate});
    }
    getBatchbyId(cid,bid){
        console.log(cid)
        console.log(bid)
        return this._http.get<any>(this._courseUrl+'/'+cid+'/batches/'+bid)
                   .catch(this.handleError)
    }
    getLectures(cid,bid){
        return this._http.get<any>(this._courseUrl+'/'+cid+'/batches/'+bid+'/lectures')
                   .catch(this.handleError)

    }
    getStudents(cid,bid){
        return this._http.get<any>(this._courseUrl+'/'+cid+'/batches/'+bid+'/students')
                   .catch(this.handleError)

    }
    addStudent(cid,bid,sname){
        return this._http.post(this._courseUrl+'/'+cid+'/batches/'+bid+'/students',{name:sname});
        
    }
     addLecture(cid,bid,lname,sid,tid){
        return this._http.post(this._courseUrl+'/'+cid+'/batches/'+bid+'/lectures',{name:lname,subjectId:sid,teacherId:tid});
        
    }

    private handleError(err:HttpErrorResponse){
        console.log(err.message);
        return Observable.throw(err.message)
    }

}



