import { Component, OnInit } from '@angular/core';
import { StudentService } from "./student.service";
import { BatchService } from "../batch/batch.service";

@Component({
  selector: 'app-add-student',
  templateUrl: './add-student.component.html',
  styleUrls: ['./add-student.component.css']
})
export class AddStudentComponent implements OnInit {
  constructor(private _studentService:StudentService,private _batchService:BatchService) { }
  submitted:boolean;
  availbatches:any[]=[];
  selectedBatches:any[];
  batchid:number;
  batches:any[];
  name:string;

  onSubmit() { this.submitted = true;
                this._studentService.addStudent(this.name,this.batchid).subscribe()
         }

  ngOnInit(){
     this.submitted=false;
     this._batchService.getBatches().subscribe(result=>{console.log(result)
                                               this.batches=result.filter((batch)=>{
                                                    return batch.startAt>new Date().toISOString().slice(0,10)
                                               })
                                                  }
                                                )
  }
}



