import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {FormsModule} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http'
import {RouterModule} from '@angular/router'
import { AppComponent } from './app.component';
import { BatchComponent } from './batch/batch.component';
import { CourseListComponent } from './courses/course-list.component';
import { CourseComponent } from './courses/course.component';
import { StudentComponent } from './students/student.component';
import { StudentListComponent } from './students/student-list.component';
import { TeacherComponent } from './teachers/teacher.component';
import { TeacherListComponent } from './teachers/teacher-list.component';
import { SubjectComponent } from './subjects/subject.component';
import { SubjectListComponent } from './subjects/subject-list.component';
import { AddSubjectComponent } from './subjects/add-subject.component';
import { AddTeacherComponent } from './teachers/add-teacher.component';
import { AddStudentComponent } from './students/add-student.component';
import { AddCourseComponent } from './courses/add-course.component';
import { SelectedCourseComponent } from './courses/selected-course.component';
import { SelectedbatchComponent } from './courses/selectedbatch.component';
import { SelectedBatchStudentsComponent } from './courses/batches/selected-batch-students.component';


@NgModule({
  declarations: [
    AppComponent,
    BatchComponent,
    CourseListComponent,
    CourseComponent,
    StudentComponent,
    StudentListComponent,
    TeacherComponent,
    TeacherListComponent,
    SubjectComponent,
    SubjectListComponent,
    AddSubjectComponent,
    AddTeacherComponent,
    AddStudentComponent,
    AddCourseComponent,
    SelectedCourseComponent,
    SelectedbatchComponent,
    SelectedBatchStudentsComponent,
    
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    RouterModule.forRoot([
      {path:'welcome',component:BatchComponent},
      {path:'courses',component:CourseComponent,children: [
          { path: 'add', component:AddCourseComponent},
           { path: 'students/:id/:bid', component:SelectedBatchStudentsComponent},
          { path: ':id/:bid', component:SelectedbatchComponent},
          {path: ':id', component: SelectedCourseComponent,children: [
           ]},
          { path: '', component:CourseListComponent},
        ]},
      {path:'students',component:StudentComponent,children: [
          { path: 'add', component:AddStudentComponent},
          { path: '', component:StudentListComponent},
        ]},
      {path:'teachers',component:TeacherComponent,children: [
          { path: 'add', component:AddTeacherComponent},
          { path: '', component:TeacherListComponent},
        ]},
      {path:'subjects',component:SubjectComponent,children: [
          { path: 'add', component:AddSubjectComponent},
          { path: '', component:SubjectListComponent},
        ]},
      {path:'',redirectTo:'welcome',pathMatch:'full'},
      {path:'**',redirectTo:'welcome',pathMatch:'full'}
    ])   
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



